"use client";

import React, { useState } from "react";
import { useRouter } from "next/navigation";
import styles from "./styles.module.css";

export default function BookingPage() {
  const router = useRouter();

  // Form state
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [tickets, setTickets] = useState(1);
  const [agree, setAgree] = useState(false);

  const isValid =
    name.trim().length > 1 && /\S+@\S+\.\S+/.test(email) && tickets > 0 && agree;

  return (
    <div className={styles.page}>
      <header className={styles.header}>
        <div className={styles.headerInner}>
          <h1 className={styles.title}>Booking</h1>
          <p className={styles.subtitle}>Confirm your details to continue</p>
        </div>
      </header>

      <main className={styles.container}>
        {/* Summary card */}
        <section className={styles.card}>
          <div className={styles.summaryTop}>
            <div className={styles.posterStub} aria-hidden />
            <div className={styles.summaryText}>
              <h2 className={styles.movieTitle}>Example Movie Title</h2>
              <div className={styles.metaRow}>
                <span className={styles.metaLabel}>Showtime</span>
                <span className={styles.metaValue}>Sept 25, 2025 Â· 7:30 PM</span>
              </div>
              <div className={styles.metaRow}>
                <span className={styles.metaLabel}>Auditorium</span>
                <span className={styles.metaValue}>3</span>
              </div>
            </div>
          </div>
        </section>

        {/* Form card */}
        <section className={styles.card}>
          <h3 className={styles.sectionTitle}>Your Details</h3>

          <form
            className={styles.form}
            onSubmit={(e) => {
              e.preventDefault();
              alert("Prototype: booking submitted (no backend)!");
            }}
          >
            <div className={styles.grid}>
              <div className={styles.field}>
                <label htmlFor="name">Full Name</label>
                <input
                  id="name"
                  type="text"
                  value={name}
                  placeholder="Your name"
                  onChange={(e) => setName(e.target.value)}
                />
              </div>

              <div className={styles.field}>
                <label htmlFor="email">Email</label>
                <input
                  id="email"
                  type="email"
                  value={email}
                  placeholder="you@example.com"
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>

              <div className={styles.field}>
                <label htmlFor="tickets">Tickets</label>
                <input
                  id="tickets"
                  type="number"
                  min={1}
                  max={10}
                  value={tickets}
                  onChange={(e) => setTickets(Number(e.target.value || 1))}
                />
                <small className={styles.hint}>Max 10 for this prototype.</small>
              </div>

              <div className={`${styles.field} ${styles.fieldCheckbox}`}>
                <label className={styles.checkbox}>
                  <input
                    type="checkbox"
                    checked={agree}
                    onChange={(e) => setAgree(e.target.checked)}
                  />
                  <span>I agree to the booking terms</span>
                </label>
              </div>
            </div>

            <div className={styles.actions}>
              <button
                type="button"
                className={styles.secondary}
                onClick={() => router.push("/movie/MOVIE_ID")}
              >
                Back
              </button>

              <button type="submit" className={styles.primary} disabled={!isValid}>
                Continue
              </button>
            </div>
          </form>
        </section>
      </main>
    </div>
  );
}